import React from 'react'
import {Container, Grid} from 'semantic-ui-react'

const Recipes = props => (
    <Container>
        <Grid>
            {props.recipes.map((recipe) =>{
                return (
                    <Grid.Column key={recipe.title}>
                        <div>
                            <h3>{recipe.title}</h3>
                            <img src={recipe.image_url}/>
                        </div>
                    </Grid.Column>
                )
            })}
        </Grid>
    </Container>
)

export default Recipes